*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* background-color: black; */
}
body{
    background-color: rgb(21, 21, 21);
}
.container{
    height: 100%;
    width: 100%;
   padding: 10px;
}


.backDark{
    background-color: rgb(27, 28, 27);
}

.leftHolder{
    /* Left section */
    border-radius: 10px;
    background-color: rgb(22, 22, 22);
    width: 23%;
    float: left;
    height: 98vh;
    display: flex;
    flex-direction: column;

    /* Left top section */
    nav{
        height: 150px;
        width: 95%;
        margin: 0 10px;
        background-color: rgb(27, 28, 27);
        padding-bottom: 10px;
        border-radius: 10px;
    }
    ul{
        height: 100%;
        width: 100%;
        list-style: none;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        margin: 10px 0 0 10px;
    }
    .nav-item{
        font-family: 'sans serif';
        display: flex;
        width: 80%;
        height: 100%;
        margin: 5px;
        a{
            width: 100%;
            width: 100%;
            text-decoration: none;
            color: white;
        }
    }
    .nav-item-img{
        width: 17px;
        height: 17px;
        margin-right: 10px;
    }

    /* Left bottom section */
    .leftSection{
        height: 100%;
        margin: 10px;
        background-color: rgb(27, 28, 27);
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between ;

        /* My library heading */
        .library{
            display: flex;
            padding: 15px 0 0 15px;
            margin-bottom: 30px;
            color: rgb(138, 136, 136);
            p{
                margin-left: 10px;
                line-height: 20px;
                font-weight: 800;
            }
        }
        .icon-image{
            width: 20px;
            height: 20px;
        }
        .icon-image-plus{
            margin-left: auto;
            margin-right: 10px;
            width: 20px;
            height: 20px;
        }

        /* Create Playlist and all */
        .div-item{
            background-color: rgb(98, 97, 97);
            margin: 10px 10px;
            border-radius: 10px;
            padding: 10px;
            color: white;
            .div-item-head{
                font-size: 14px;
                font-weight: 700;
            }
            .div-item-desc{
                margin-top: 5px;
                font-size: 12px;
                font-weight: 600;
            }
            .div-item-btn{
                background-color: white;
                margin-top: 15px;
                padding: 6px 10px;
                border-radius: 15px;
                border: 0px solid black;
                font-size: 11px;
                font-weight: 600;
            }
        }


        .leftSectionBottom{
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            .contents{
                display: flex;
                flex-wrap: wrap;
                p{
                    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
                    color: rgb(153, 155, 156);
                    margin: 5px 7px;
                }
            }
            .langBtn{
                margin:5px 15px;
                button{
                    display: flex;
                    background-color: rgb(98, 97, 97);
                    border: 0px solid black;
                    line-height: 20px;
                    padding: 4px 7px;
                    border-radius: 20px;
                    img{
                        margin-right: 5px;
                    }
                }
            }
        }
    }
}

.rightHolder::-webkit-scrollbar{
    display: none;
}

.rightHolder{
    border-radius: 20px;
    background-color: rgb(27, 28, 27);
    width: 77%;
    height: 98vh;
    float: right;
    overflow: hidden;
    overflow-y: scroll;
    .upperNav{
        height: 50px;
        width: 100%;
        display: flex;
        position: relative;
        z-index: 999;
        border-radius: 10px 10px 0 0 ;
        .leftUpperNav{
            width: 50%;
            margin: 0 10px;
            height: 100%;
            
            ul{
                height: 100%;
                width: 100%;
                display: flex;
                list-style: none;
                justify-content: flex-start;
                align-items: center;
                li{
                    height: 35px;
                    width: 35px;
                    background-color: rgb(75, 71, 71);
                    margin: 0 10px; 
                    align-items: center;
                    border-radius: 50%;
                    padding: 5px;
                }
                li:hover{
                    background-color: rgb(31, 30, 30);
                }
            }
        }
        .rightUpperNav{
            width: 50%;
            height: 50px;
            display: flex;
            justify-content: flex-end;
            ul{
                height: 100%;
                width: 50%;
                display: flex;
                list-style: none;
                gap: 10px;
                justify-content: center;
                align-items: center;
                li{
                    width: 100px;
                    height: 40px;
                    /* background-color: white;      */
                    text-align: center;
                    border-radius: 20px;
                    
                    a{
                        text-decoration: none;
                        line-height: 40px;
                        color: rgb(119, 116, 116);
                        font-weight: 700;
                    }
                    a:hover{
                        color: white;
                        font-weight: 800;
                    }
                }
                #loginBtn{
                    background-color: white;
                    a{
                        
                    color: black;
                    }
                }

            }
        }
        
    }
    
    
}
.rightContentSection::-webkit-scrollbar{
    display: none;
}

.rightContentSection{
    background-color: rgb(27, 28, 27);
    width: 100%;
    height: 93%;
    display: flex;
    gap: 10px;
    position: relative;
    flex-direction: column;
    overflow: hidden;
    overflow-y: scroll;
    .popularArtistsHeading{
        display: flex;
        background-color: rgb(27,28,27);
        justify-content: space-between;
        padding: 0 20px;
        h3{
            line-height: 40px;
            color: white;
        }
        p{
            text-overflow:ellipsis;
            font-size: 12px;
            line-height: 40px;
            color: white;
        }
    }
    
    .popularArtists{
        width: 100%;
        height: 285px;
        /* border-radius: 20px; */
        /* background-color: rgb(59, 59, 59); */
        padding: 10px;
        padding-bottom: 20px;
        display: flex;
        /* justify-content: space-evenly; */
        .profileContainer::-webkit-scrollbar{
            display: none;
        }
        .profileContainer{
            width: 100%;
            height: 100%;
            display: flex;
            overflow:hidden;   
            overflow-x: scroll;
            
            scrollbar-color: transparent;
            gap: 10px;
            /* position: relative; */
            .card{
                width: 300px;
                height: 100%;
                /* background-color: rgb(83,83,83); */
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                border-radius: 10px;
                transition: 0.5s;
                img{
                    margin: 0 20px;
                    position: relative;
                    height: 170px;
                    width: 170px;
                    border-radius: 50%;
                    /* border: 2px solid white; */
                }
                .cardImage-2{
                    margin: 10px;
                    width: 150px;
                    height: 170px;
                    border-radius: 10px;
                }
                p{
                    color: white;
                    /* text-align: center; */
                    padding-left: 10px;
                }
                .name{
                    font-size: 17px;
                }
                .details{
                    font-size:13px;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient:vertical;
                    overflow: hidden;
                }
            }
            .card:hover{
                background-image: linear-gradient(rgb(59, 59, 59),rgb(31, 30, 30));
            }
        }
    }
    
}

